<template>
  <div v-if="breakpoints.mobile.matches" class="home__tools">
    <div class="home__tools__fee d-flex justify-content-between align-items-center" @click="goToFee">
      <div class="d-flex align-items-center">
        <Icon name="info-circle" class="home__tools-fee-info"/>
        <Text ml="10px">{{ t('homePage.feeChanges') }}</Text>
      </div>
      <Icon name="arrow-right-full" class="home__tools-arrow"/>
    </div>
  </div>
  <section
    class="home"
  >
    <CoinExchange class="home__exchange" />
    <CoinForm @submit="confirmExchange" class="home__form"/>
  </section>
</template>

<script>
import { MAIN_ROUTES_NAMES } from '@/common/routes'
import { MODULES_NAMES } from '@/common/modules'
import { useBreakpointsMedia } from '@/plugins/breakpoints'
import { useMutations } from '@/plugins/vuex'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import CoinExchange from '@/views/HomePage/CoinExchange'
import toolsMock from '@/components/Tools/mock'
import CoinForm from '@/views/HomePage/CoinForm'
import Text from '@/components/Text/Text'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'HomePage',
  components: { Icon, Text, CoinForm, CoinExchange },
  setup () {
    const router = useRouter()
    const { t } = useI18n()
    const { breakpoints } = useBreakpointsMedia()
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)

    const goToFee = () => {
      router.push({ name: MAIN_ROUTES_NAMES.FEE_SCHEDULE })
    }
    const confirmExchange = () => {
      router.push({ name: MAIN_ROUTES_NAMES.EXCHANGE_CONFIRMATION })
    }

    setTools(toolsMock)

    return {
      t,
      breakpoints,
      goToFee,
      confirmExchange
    }
  }
}
</script>

