.home {
  padding: 80px 100px 82px 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  &__exchange {
    margin-bottom: 30px;
  }
  &__tools {

    &-fee-info {
      opacity: 0.4;
    }
    &-arrow {
      opacity: 0.4;

      svg path {
        stroke: map-get($colors, "primary");
      }
    }
    &__fee {
      padding: 12px 21px;
